.container {
  align-items: start;
  gap: 50px;
  margin-top: 15px;
  display: flex;
}

.breed-select {
  width: 200px;
  color: #000;
  background-color: rgba(102, 101, 97, .61);
  border-color: #000;
  margin-left: 20px;
  font-weight: bold;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 8px solid rgba(0, 0, 0, .31);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 40px;
  height: 40px;
  border: 3px solid rgba(0, 0, 0, 0);
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.hidden {
  display: none;
}

.cat-info {
  gap: 15px;
  display: flex;
}

h1 {
  margin: 0;
}

p {
  width: 400px;
  font-size: 20px;
}

